.searchSelect {
	width: 100%;
	height: 35px;

	border: none;
	outline: 0;
	background-color: #2A3038;
	color: #999;
}

.searchOption {
	font-size: 1.1rem;
	padding: 5px 0;
}


@media (min-width: 480px) {
	.searchSelect {
		min-height: 120px;
	}
}



